import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api.service';
@Component({
  selector: 'app-app-config',
  templateUrl: './app-config.component.html',
  styleUrls: ['./app-config.component.css']
})
export class AppConfigComponent implements OnInit {
	data: any = {};
	isSuccessAlert: any = false;
	isDangerAlert: any = false;
  constructor(
  	public api: ApiService
  	) { }

  ngOnInit(): void {
  	this.getData();
  }

  getData(){
  	this.api.provideData({
  		type: 'getappconfigadm'
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.data = resp[0];
  		console.log(this.data);
  	});
  }

  saveProgram(){

  	this.api.provideData({
  		type: 'editappconfigadm',
  		app_version: this.data.app_version,
  		app_store_url: this.data.app_store_url,
  		app_id: this.data.app_id
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.isSuccessAlert = true;
  		setTimeout(()=>{ 
					    this.isSuccessAlert = false;
					    this.getData();

					 }, 2000);
  	});

  }

}
